@import './keyframes';

*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  height: 3000px;
  margin: 0;

  background: linear-gradient(0deg, rgba(0,255,0,0.5) 0%, rgba(0,0,255,0.5) 100%);
}

.container {
  max-width: 100%;
  width: 600px;
}

.modal {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  max-width: 100%;
  max-height: 100%;
  justify-content: center;
  align-items: center;

  // &--transitionable {
  //   .modal {
  //     &__container {
  //       opacity: 0;
  //       transform: translateY(-100px);
  //       transition-duration: 300ms;
  //       transition-timing-function: ease-in-out;
  //     }
  //     &__backdrop {
  //       opacity: 0;
  //       transition-duration: 200ms;
  //     }
  //   }
  // }

  &--visible {
    display: flex;
  }

  &-animation {
    &-backdrop {
      &--show {
        animation: fadeIn 200ms linear both;
      }
      &--hide {
        animation: fadeOut 200ms linear both;
      }
    }
    &-container {
      &--show {
        animation: translateIn 300ms linear both;
      }
      &--hide {
        animation: translateOut 300ms linear both;
      }
    }
    &--cancel {
      animation: none !important;
    }
  }

  &__backdrop {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(black, 0.3);
    opacity: 0;
    transition-duration: 300ms;

    &.modal {
      &-animation {
        &-backdrop {
          &--show {
            animation: fadeIn 200ms linear both;
          }
          &--hide {
            animation: fadeOut 200ms linear both;
          }
        }
        &--cancel {
          animation: none !important;
        }
      }
      &-transition {
        &-backdrop {
          &--show {
            opacity: 1;
          }
          &--hide {
            opacity: 0;
          }
        }
        &--cancel {
          transition: none !important;
        }
      }
    }
  }
  &__container {
    position: relative;
    width: 500px;
    height: 500px;
    border: solid 1px black;
    background-color: #fff;
    opacity: 0;
    transform: translateY(-100px);
    transition-duration: 300ms;

    &.modal {
      &-animation {
        &-container {
          &--show {
            animation: translateIn 300ms linear both;
          }
          &--hide {
            animation: translateOut 300ms linear both;
          }
        }
        &--cancel {
          animation: none !important;
        }
      }
      &-transition {
        &-container {
          &--show {
            opacity: 1;
            transform: translateY(0);
          }
          &--hide {
            opacity: 0;
            transform: translateY(100px);
          }
        }
        &--cancel {
          transition: none !important;
        }
      }
    }
  }
}